/// Calcula el tamao de una columna
///
/// @param {number} $col_width: tamao real de la columna
/// @param {number} $container_width: tamao de de contenedor
///
/// @author Diego Villaseor y Cecilia M. Borja
///	@since 0.0.1
///

@function column_percentage($col_width, $container_width) {
	@return $col_width/$container_width*100%;
}
